.country_card {
  width: 200px;
  height: 200px;
  border: 2px solid lightgray;
  border-radius: 10px;
  box-shadow: 5px 5px 10px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: transform 0.3s ease;
  background-color: rgba(255, 255, 255, 0.45);
  padding: 1rem;

  &:hover {
    transform: scale(1.05);
  }

  @media all and (min-width: 1200px) {
    width: 250px;
    height: 250px;
  }

  &-img-container {
    width: 90%;
    max-height: 73px;
    margin: auto;

    .img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center;
    }
  }

  &-name {
    padding: 0.5rem;
    white-space: wrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 160px;
    height: 80px;
    position: relative;
    font-weight: 600;
    font-size: 14px;
    text-align: left;

    @media all and (min-width: 768px) {
      font-size: 16pxs;
    }
  }

  &-heart {
    width: 100px;
    height: 100px;
    position: absolute;
    top: 80%;
    left: 70%;
    z-index: 1000;

    @media all and (min-width: 768px) {
      left: 76%;
    }
    @media all and (min-width: 1200px) {
      left: 82%;
    }

    &-img {
      width: 40%;
    }
  }
}
